import { Card, CardContent, CardHeader, Typography } from "@mui/material";

function Projects(){
    return (
        <div className="About">
            <Card>
            <CardHeader title="Moje projekty"/>            
            <CardContent>
                <Typography> 
                W mojej karierze miaem przyjemno pracowa nad rnorodnymi projektami. Niezalenie od skali projektu, zawsze staram si dostarczy rozwizania, ktre przynosz warto i zadowolenie klientom. Kilka z moich projektw mona zobaczy w sekcji portfolio.
                </Typography> 
            </CardContent>
            </Card>
        </div>
    )
}

export default Projects;