import { Card, CardContent, CardHeader, Typography } from "@mui/material";

function About(){
    return (
        <div className="About">
            <Card>
            <CardContent>
                <Typography> 
                    Witaj! Nazywam si Marcin Kozowski, i jestem pasjonatem tworzenia aplikacji i rozwiza opartych na technologii .NET. Moja przygoda z programowaniem rozpocza si wiele lat temu, i od tego czasu nieustannie doskonal swoje umiejtnoci, aby dostarcza innowacyjne i efektywne rozwizania dla klientw i uytkownikw.
                </Typography>
            </CardContent>
            <CardHeader title="Moja historia"/>            
            <CardContent>
                <Typography> 
                    Moja fascynacja programowaniem zacza si, gdy po raz pierwszy zetknem si z C#. Ta wszechstronna i potna technologia zapocztkowaa moj przygod z .NET, a od tego czasu nie patrzyem ju wstecz. Pracujc w rnych projektach, od maych startupw po due przedsibiorstwa, zdobyem cenne dowiadczenie w projektowaniu, rozwijaniu i utrzymaniu aplikacji na platformie .NET.
                </Typography> 
            </CardContent>
            </Card>
        </div>
    )
}

export default About;